.about-me {
    /* background-image: url('https://images.unsplash.com/photo-1503264116251-35a269479413?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1050&q=80'); */
    min-height: calc(100vh - 100px);
    max-width: 90vw;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}
.about-col {
    /* background-color: aliceblue; */
    display: flex;
    flex-direction: column;
    align-items: center;
}
.col-left {
    width: 30%;
    box-sizing: border-box;
    padding: 50px 0;
    /* background-color: aliceblue; */

}
.col-right {
    width: 69%;

}
#my-name {
    color: rgb(190, 190, 190);
}
.about-me-header {
    font-size: xx-large;
    margin: 10px;
    font-weight: bold;
    color: rgb(190, 190, 190);
}
#img-me {
    width: 90%;
    border-radius: 50%;
    margin: 10px 0;
}
#about-me-p {
    width: 90%;
    text-align: justify;
    margin: 0;
    padding: 0;
}
#cv {
    text-decoration: underline;
    color:coral;
}
#cv:hover {
    cursor: pointer;
}
#cv {
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}
  
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
        color: cyan;
    }
    100% {
        opacity: 1;
        color: coral;
     }
}
.pern-expanded {
   display: none;
}
.pern {
    text-decoration: underline;
}
.pern:hover {
    cursor: pointer;
}
.pern:hover ~ .pern-expanded {
    display: inline;
}
.skills-container {
    width: 90%;
    box-sizing: border-box;
    padding: 10px ;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.skill {
    /* background-color: aqua; */
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin:  5px 0;
}
.skill label {
    width: 30%;
    text-align: left;
}
.skill-bar {
    width: 70%;
    height: 20px;
    background-color:  #4d4d52;
    border-radius: 10px;
}
.skill-fill {
    background: linear-gradient(90deg, rgba(12,41,144,1) 26%, rgba(16,175,187,1) 76%);
    height: 100%;
    border-radius: 10px;
}
#skill-js {
    width: 95%; 
}
#skill-html {
    width: 95%;
}
#skill-css {
    width: 90%;
}
#skill-ne {
    width: 95%;
}
#skill-api {
    width: 95%;
}
#skill-react {
    width: 95%;
}
#skill-pql {
    width: 99%;
}

#skill-rx {
    width: 50%; 
}
#skill-asp {
    width: 50%;
}
#skill-xam {
    width: 50%;
}
#skill-mysql {
    width: 60%;
}
#skill-aws {
    width: 50%;
}
#skill-php {
    width: 40%;
}

a {
    text-decoration: none;
    text-decoration: underline;
    color: #a9a9b3;
}
a:visited {
    text-decoration: none;
    text-decoration: underline;
    color: #a9a9b3;
}

@media only screen and (max-width: 600px) {
    body {
        padding: 0;
    }
    .about-me {
        
        flex-direction: column;
        margin: 0;
        align-items: center;
        align-content: center;
    }
    .col-left {
        width: 100%;

    }
    .col-right {
        width: 100%;
    }
    .skills-container {
        width: 100%;
    }
    .skill {
        flex-direction: column;
    }
    .skill label {
        width: 100%;
        margin-bottom: 5px;
    }
    .skill-bar {
        width: 100%;
        height: 20px;
        background-color:  #4d4d52;
        border-radius: 10px;
    }
  }